import React, { Component } from 'react'
import Main from '../template/Main'
import axios from 'axios'



const headerProps = {
    icon: 'address-card',
    title: 'Membros',
    subtitle: 'Cadastrar, verificar, editar ou excluir'
}

const baseUrl = 'http://localhost:3001/users'
const initialState = {
    user: {name: '', mainClass: '', igID: ''},
    list: []
}

export default class UserCrud extends Component {
    state = {...initialState}

    componentWillMount(){
        axios(baseUrl).then(resp => {
            this.setState({ list: resp.data })
        })
    }

    clear(){
        this.setState({user: initialState.user})
    }

    save(){
        const user = this.state.user
        const method = user.id ? 'put' : 'post'
        const url = user.id ? `${baseUrl}/${user.id}` : baseUrl
        axios[method](url, user)
            .then(resp => {
                const list = this.getUpdatedList(resp.data)
                this.setState({user: initialState.user, list})
            })
    }

    getUpdatedList(user, add = true){
        const list = this.state.list.filter(u => u.id !== user.id)
        if(add) list.unshift(user)
        return list
    }

    updateField(event){
        const user = { ...this.state.user }
        user[event.target.name] = event.target.value
        this.setState({ user })
    }

    renderForm(){
        return(
            <div className="form">
                <div className="row">
                    <div className="col12 col-md-6">
                        <div className="form-group">
                            <label>Nick</label>
                            <input type="text" className="form-control" name="name"
                            value={this.state.user.name}
                            onChange={e=> this.updateField(e)} placeholder="Digite o Nick do seu personagem"/>
                        </div>
                    </div>
                    <div className="col-12 col-md-6">
                        <div className="form-group">
                            <label>Classe Main</label>
                            <input type="text" className="form-control" name="mainClass"
                            value={this.state.user.mainClass}
                            onChange={e => this.updateField(e)}
                            placeholder="Sua classe Main"/>
                        </div>
                    </div>
                    <div className="col-12 col-md-6">
                        <div className="form-group">
                            <label>ID do personagem</label>
                            <input type="text" className="form-control" name="igID"
                            value={this.state.user.igID}
                            onChange={e => this.updateField(e)}
                            placeholder="Digite o ID do personagem"/>
                        </div>
                    </div>
                </div>
                <hr/>
                <div className="row">
                    <div className="col-12 d-flex justify-content-end">
                        <button className="btn btn-primary"
                            onClick={e=> this.save(e)}>Salvar</button>
                        <button className="btn btn-secondary ml-2"
                            onClick={e=> this.clear(e)}>Cancelar</button>
                    </div>
                </div>
            </div>
        )
    }

    load(user){
        this.setState({user})
    }

    remove(user){
        axios.delete(`${baseUrl}/${user.id}`).then(resp => {
            const list = this.getUpdatedList(user, false)
            this.setState({ list })
        })
    }

    renderTable(){
        return(
            <table className="mt-4 col-12">
                <thead>
                     <tr>
                         <th>ID</th>
                         <th>Nick</th>
                         <th>Classe Main</th>
                     </tr>
                </thead>
                <tbody>
                    {this.renderRows()}
                </tbody>
            </table>
        )
    }

    renderRows(){
        return this.state.list.map(user =>{
            return(
                <tr key={user.id}>
                    <td>{user.igID}</td>
                    <td>{user.name}</td>
                    <td>{user.mainClass}</td>
                    <td>
                        <button className="btn btn-warning"  onClick={() => this.load(user)}>
                            <i className="fa fa-pencil"></i>
                        </button>
                        <button className="btn btn-danger ml-2"  onClick={() => this.remove(user)}>
                            <i className="fa fa-trash"></i>
                        </button>
                        <button className="btn btn-success ml-2" >
                            <i className="fa fa-info"></i>
                        </button>
                    </td>
                </tr>
            )
        })
    }

    renderWarning(){
        return(
            <div className='data-warning col-12 col-md-6'>
                <p>**Esta versão é apenas uma demonstração do front-end da aplicação.
                    O back-end foi implementado apenas no desenvolvimento, para teste de persistência dos dados </p>
            </div>
        )
    }

    render(){
        return (
            <Main {...headerProps}>
                {this.renderForm()}
                {this.renderWarning()}
                {this.renderTable()}
            </Main>
        )
    }
}